import { UserApi } from "../api/api"
import { stopLoader } from './finding-reducer';

const FOLLOW_FRIEND = 'follow-friend'
const SET_USERS = 'set-users'
const SET_NUMBER_PAGE = 'set-count-page'
const SET_COUNT = 'set-count'
const TOGGLE_FOLLOWING = 'toggle-following'

let stateInitial = {
    users: [],
    numberPage : 0,
    countPage : 1,
    numberName: 10,
    toggleFollowingProgress: false,
    thisId : '0'
}


export let userReducer = (state = stateInitial,action) => {

    let stateCopy = {
        ...state,
        users:[...state.users]
    }
    
    
    switch(action.type) {
        case FOLLOW_FRIEND :
            if(action.bolean === true) {
                stateCopy.users.map(item => {
                    if(item.id === action.id) {
                        item.followed = true

                    }
                }
                )
            } else {
                stateCopy.users.map(item => {
                    if(item.id === action.id) {
                        item.followed = false
                      
                    }
                }
                )
            }
        return stateCopy
        case SET_USERS :
            stateCopy.users = action.users//[...action.users]
            return stateCopy
        case SET_NUMBER_PAGE :
            stateCopy.numberPage = action.number
            return stateCopy
        case SET_COUNT : 
            stateCopy.countPage = action.number
            return stateCopy
        case TOGGLE_FOLLOWING :
            if(action.bolean === true) {
                stateCopy.toggleFollowingProgres = true
            } else if (action.bolean === false) {
                stateCopy.toggleFollowingProgres = false
            } 
            return stateCopy
        default: return state
    }
}

export let followFriend = (id,bolean) => ({type:FOLLOW_FRIEND,id:id,bolean:bolean})

export let setUsers = (users) => ({type:SET_USERS,users:users})

export let setNumberPage = (number) => ({type:SET_NUMBER_PAGE, number:number})

export let setCount = (number) => ({type: SET_COUNT, number:number})

export let toggleFollowing = (bolean) => ({type:TOGGLE_FOLLOWING,bolean:bolean })


export let getNumberThunk = (numberName) => {
    return async (dispatch) => {

        let data = await UserApi.getBtn()

        dispatch(setNumberPage(Math.ceil(data.totalCount / numberName))) 
        dispatch(stopLoader())

    }
}

export let getUsersThunk = (numberName,countPage) => {
    return async (dispatch) => {

        let data = await UserApi.getUsers(numberName,countPage)

        dispatch(setUsers(data.items))
        dispatch(stopLoader())

    }
}

let followUnfollowThunk = (id,thunk,bolean) => async(dispatch) => {
    let data = await thunk(id)

    dispatch(toggleFollowing(bolean) )
    dispatch(stopLoader())
}

export let deleteUsersThunk = (id) => {
    return followUnfollowThunk(id,UserApi.deleteUser,false)
}

export let postUsersThunk = (id) => {
    return followUnfollowThunk(id,UserApi.postUser,true)
}
