const NEW_MESSAGE_CREATE = 'NewMessegeCreate'

let inithialState = {
    NameData : [{name:'Dima', id: '1', image: 'https://www.125./upload/iblock/17f/17fcd7be6ed746cf9dc27f5fa861a498.jpg'},{name:'Alex', id: '2', image : 'https://w.forfun.com/fetch/e6/e6044cb0b978ce39ff76b57402ebd1de.jpeg '},{name:'Lexa', id: '3', image : 'https://w.forfun.com/fetch/e6/e6044cb0b978ce39ff76b57402ebd1de.jpeg '}],
    MessData : [
        {
            mymes:'Lorem, ipsum dolor sit amet consectetur adipisicing elit. Corporis, consequuntur sequi. Beatae sunt optio quisquam consequatur ratione. Ullam, at expedita Lorem, ipsum dolor sit amet consectetur adipisicing elit. Corporis, consequuntur sequi. Beatae sunt optio quisquam consequatur ratione. Ullam, at expedita?', 
            youmes:''
        },
        {
            mymes:'', 
            youmes:'Lorem, ipsum dolor sit amet consectetur adipisicing elit. Corporis, consequuntur sequi. Beatae sunt optio quisquam consequatur ratione. Ullam, at expedita Lorem, ipsum dolor sit amet consectetur adipisicing elit. Corporis, consequuntur sequi. Beatae sunt optio quisquam consequatur ratione. Ullam, at expedita?'
        }
    ],
}

export const dialosReducer = (state = inithialState , action) => {

    let stateCopy = {
        ...state,
        NameData:[...state.NameData],
        MessData: [...state.MessData]
    }
    switch(action.type) {
        case NEW_MESSAGE_CREATE :
            let NewMessage = {mymes: action.text , youmes:''};
            stateCopy.MessData.push(NewMessage);
            return stateCopy;
        default : return state;
    }

}


export const newMessegeActionCreator = (text) => ({type:NEW_MESSAGE_CREATE,text:text})