import './App.css';
import React, { Suspense } from 'react';
// import DialogsContainer from './components/Dialogs/DialogsContainer';
import { Route, Routes, BrowserRouter } from 'react-router-dom';
import SitebarContainer from './components/Sitebar/sitebarContainer';
// import UsersContainer from './components/User/UsersContainer';
import Loader from './comon/loader/loader';
import LoginContainer from './components/LoginContainer/LoginContainer'
import { HeaderConatiner } from './components/Header/HeaderContainer';
// import ProfileContainer from './components/Profile/ProfileContainer';
import WindowContainer from './components/Window/WindowContainer';

const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'))
const UsersContainer = React.lazy(() => import('./components/User/UsersContainer'))
const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'))

const App = React.memo( ({toFinding,authComplete,authCompleteAC,name,stopLoader,startLoader,id}) => {

  
  
  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <div className='grid' >
        <HeaderConatiner />
        <SitebarContainer />
        {toFinding ? <Loader /> : null}
        <div className="wrapper__content" style={toFinding ? {overflow: 'hidden'}: {overflow: 'scroll'}}>
          <Routes>
            <Route path="/profile/*" element={
              // <Suspense fallback={<Loader /> }>
              <Suspense fallback={<div >Loading...</div>}>
                <ProfileContainer />
              </Suspense>
            }/>
            <Route path="/dialogs" element={
              <Suspense fallback={<Loader /> }>
                <DialogsContainer />
              </Suspense>
            } />
            <Route path="/users" element={
              <Suspense fallback={<Loader /> }>
                <UsersContainer />
              </Suspense>
            } />
            <Route path="/login" element={<LoginContainer/>} />
          </Routes>
        </div>
        <WindowContainer />
      </div>
    </BrowserRouter>
  )
})
export default App;
//path="/profile" Component={Profile}/
//path="/dialogs" Component={Dialogs}/