import React from "react";
import { connect } from "react-redux";
import Window from './Window';
import { authCompleteAC } from './../../redux/auth-reducer';
import { stopLoader,startLoader } from './../../redux/finding-reducer';
import { getAuthComplete, getId, getName } from "../../redux/redux-select/auth-reselect";


let f1 = (state) => {
    return {
        authComplete: getAuthComplete(state),
        name:getName(state),
        id: getId(state),
    }
}

let WindowContainer = connect(f1, {
    authCompleteAC,
    stopLoader,
    startLoader,
})(Window)

export default WindowContainer