import s from './Input.module.css'

export const LoginInput = (props) => {
    return (
        <div className="">
            <input {...props.input} className={`${s.login__input} ${props.meta.error && props.meta.touched && s['login__input--error']}`} />
            <span className={props.meta.error && props.meta.touched && s.error__text}>{props.meta.touched && props.meta.error}</span>
        </div>
    )
}

