import React from "react";
import classes from './sitebar.module.css';
import Friend from './Friends/Friend';
import { NavLinkCreator } from './../../comon/NavLink/NavLink';
import { NavLink } from "react-router-dom";

let setActive = ({ isActive }) => isActive ? `${classes['menu__list-link']} ${classes.activeLink}` : `${classes['menu__list-link']}`

const sitebar = React.memo( (props) => {

  let clickLoader = () => {
    props.startLoader()
  }

  let NoclickLoader = () => {
    console.log('re');
  }

  return (
    <div className={classes.sitebar}>
      <nav className={classes.sitebar__menu}>
        <ul className={classes.menu__list} type='none'>
          <li className={classes['menu__list-item']}>
            {NavLinkCreator(clickLoader , setActive, 'Profile', '/profile/' + `${props.id ? props.id : null }`)}
          </li>
          <li className={classes['menu__list-item']}>
            {NavLinkCreator(NoclickLoader, setActive, 'Dialogs','/dialogs')}
          </li>
          <li className={classes['menu__list-item']}>
            <a href="" className={classes['menu__list-link']}>News</a>
          </li>
          <li className={classes['menu__list-item']}>
            <a href="" className={classes['menu__list-link']}>Music</a>
          </li>
        </ul>
      </nav>
      <div className={classes.sitebar__user}>
        {NavLinkCreator(clickLoader, setActive, 'Find user','/users')}
      </div>
      <div className={classes.sitebar__settings}>
        <a href="" className={classes['sitebar__settings-link']}>Settings</a>
      </div>
      <div className={classes.sitebar__friend}>
        <Friend img={props.state[0].image} state={props.state[0].name} />
        <Friend img={props.state[1].image} state={props.state[1].name} />
        <Friend img={props.state[2].image} state={props.state[2].name} />
      </div>
    </div>
  )
})

export default sitebar;