import { connect } from "react-redux";
import Header from './Header';
import { startLoader, stopLoader } from './../../redux/finding-reducer';
import { LogoutThunk } from './../../redux/auth-reducer';
import { getId, getName, getStatus } from "../../redux/redux-select/auth-reselect";

let f1 = (state) => {
    return {
        status : getStatus(state),
        name : getName(state),
        id : getId(state)
    }
}

export let HeaderConatiner = connect(f1, {
    startLoader,
    LogoutThunk,
    stopLoader
})(Header) 