import {applyMiddleware, combineReducers, compose, legacy_createStore as createStore} from "redux"; 
import { profileReducer } from './profile-reducer';
import { dialosReducer } from './dialogs-reducer';
import { userReducer } from './user-reducer';
import { findingReducer } from './finding-reducer';
import authReducer from './auth-reducer';
import thunkMiddelware from 'redux-thunk'
import { reducer as formReducer } from 'redux-form'

let reducers = combineReducers({
    profileData: profileReducer,
    dialogsData: dialosReducer,
    usersData: userReducer,
    finding : findingReducer,
    authData : authReducer,
    form : formReducer,
})

const composeWithDevTools = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose

let store = createStore(reducers, composeWithDevTools(applyMiddleware(thunkMiddelware)))

export default store