const START_LOADER = 'start-loader'
const STOP_LOADER = 'stop-loader'

let stateInition = {
    toFinding : false
}

export const findingReducer = (state = stateInition, action) => {
    let stateCopy
    switch (action.type) {
        case START_LOADER : 
            stateCopy = {...state}
            stateCopy.toFinding = true;
            return stateCopy
        case STOP_LOADER :
            stateCopy = {...state}
            stateCopy.toFinding = false;
            return stateCopy
        default:
            return state
    }
}

export let startLoader = () => ({type: START_LOADER})

export let stopLoader = () => ({type: STOP_LOADER})