import React from "react";
import s from './Friend.module.css'

const Friend = (props) => {
    return (
        <a href="" className={s['sitebar__friend-box']}>
            <img src={props.img} className={s['sitebar__friend-img']} />
            <p className={s['sitebar__friend-link']} href="">{props.state}</p>
        </a>
    )
}

export default Friend

