import { LoginAPI } from "../api/api"

const GET_DATA = 'get-data'
const LOGOUT = 'logout'
const AUTH_COMPLETE = 'auth-complete'

let stateInition = {
    email: '' ,
    login: '' ,
    pass : '',
    data: {
        data: {
            id: '',
            login: '',
            email: '',
        }
    },
    messages : [],
    status : false,
    authComplete: false
}

const authReducer = (state = stateInition,action) => {
    
    let stateCopy = {...state}
    switch (action.type) {
        case GET_DATA : 
            stateCopy.data = {...action.data}
            stateCopy.status = true
            return stateCopy
        case LOGOUT :
            stateCopy.status = false
            stateCopy= {
                email: '' ,
                login: '' ,
                pass : '',
                data: {
                    data: {
                        id: '',
                        login: '',
                        email: '',
                    }
                },
                messages : [],
                status : false,
                authComplete: false
            }
            return stateCopy
        case AUTH_COMPLETE :
            if(action.bolean === true) {
                stateCopy.authComplete = true
            } else {
                stateCopy.authComplete = false
            }
            return stateCopy
        default:
            return state;
    }
    
}

export default authReducer

export let getData = (data) => ({type:GET_DATA, data:data})

export let authCompleteAC = (bolean) => ({type:AUTH_COMPLETE, bolean:bolean})

let logout = () => ({type:LOGOUT})

export let getDataThunk = () => {
    return async (dispatch) => {
       let data = await LoginAPI.getData()
            
        dispatch(getData(data))
       
    }
}

export let LoginThunk = (password,login) => async (dispatch) => {
    let data = await LoginAPI.login(password,login)

    if(data.resultCode === 0) {
        dispatch(getDataThunk())
        dispatch(authCompleteAC(true))
    }
   
}

export let LogoutThunk = () => async (dispatch) => {
    let data = await LoginAPI.logout()

    if(data.resultCode === 0) {
        dispatch(logout())
    }
   
}