import React, { PureComponent, useEffect } from "react";
import { connect } from "react-redux";
import LoginForm from './LoginForm';
import { getDataThunk, getData, LoginThunk } from './../../redux/auth-reducer';
import { stopLoader,startLoader } from './../../redux/finding-reducer';
import s from './LoginForm.module.css'
import { reduxForm } from "redux-form";
import { getStatus } from "../../redux/redux-select/auth-reselect";

let Login = React.memo((props) => {
    // componentDidMount() {
    //     this.props.stopLoader()
    // }

    let onSubmit = (formData) => {
        props.LoginThunk(formData.login,formData.password);
        props.startLoader()
    }
    
    useEffect(() => {
        
        props.stopLoader()

    },[props.stopLoader])
        
    return (
        <div className="">
            <h1 className={s.login__title}>  </h1>
            <ReduxLoginForm status={props.status} onSubmit={onSubmit} />
        </div>
    )
    
})

let ReduxLoginForm = reduxForm({
    form: 'sing-in'
})(LoginForm)

let f1 = (store) => {
    return {
        status: getStatus(store)
    }
}

let LoginContainer = connect(f1,
    {
        stopLoader,
        startLoader,
        getDataThunk,
        getData,
        LoginThunk
    })(Login)

export default LoginContainer