import React from "react";
import classes from './header.module.css';
import { NavLink } from 'react-router-dom';
import img from './exit-door-svgrepo-com.svg'
import { NavLinkCreator } from './../../comon/NavLink/NavLink';

const Header = React.memo((props) => {

  let Logout = () => {
    props.stopLoader()
    props.LogoutThunk()
  }

  let func = () => {
    props.startLoader()
  }

    return (
        <header className={classes.header}>
          <a href="#" className={classes.header__logo}>
            <img src="https://i.pinimg.com/originals/b9/05/3d/b9053d873e9f69058997913e0fffca2e.png" alt="" className={classes["header__logo-img"]}/>
          </a>
          <button onClick={() => Logout()} className={classes.header__btn}>
            <img  src={img} className={classes['header__btn-img']} />
          </button>
          {props.status ? NavLinkCreator(func,classes.header__link,props.name,`profile/${props.id}`) : NavLinkCreator(func,classes.header__link,'Login',`profile/${props.id}`)}
        </header>
    );
})

export default Header;