import axios from "axios";

let inition = axios.create({
    withCredentials:true,
    headers : {
        "api-key" : 'b40def35-f67d-451e-ae99-704bd4949cb2'
    },
    baseURL: 'https://social-network.samuraijs.com/api/1.0'
})

export const UserApi = {
    getBtn ()  {
       return inition.get('/users').then(response => {return response.data})
    },
    getUsers (count,page) {
        return inition.get(`/users?count=${count}&page=${page}`).then(response => {return response.data})
    },
    postUser (id) {
        return inition.post(`follow/${id}`).then(response => {return response.data})
    },
    deleteUser (id) {
        return inition.delete(`follow/${id}`).then(response => {return response.data})
    }
}

export const LoginAPI = {
    getData () {
        return inition.get('auth/me').then(response => {return response.data})
    },
    login (email, password) {
        return inition.post('auth/login', {email, password}).then(response => {return response.data})
    },
    logout () {
        return inition.delete('auth/login').then(response => {return response.data})
    }
}

export const ProfileApi = {
    getUserData (id) {
        return inition.get(`profile/${id}`).then(response => {return response.data})
    },
    updateStatus (status){
        return inition.put(`profile/status`, {status: status}).then(response => {return response.data})
    },
    getStatus (id) {
        return inition.get(`profile/status/${id}`).then(response => {return response.data})
    }
}

