import { stopLoader } from './finding-reducer';
import { ProfileApi } from './../api/api';

const ADD_POST = "ADD-POST"
const PROFILE_UPDATE = 'profile-update'
const SET_DATA = 'set-data'
const SET_ID = 'set-id'
const NEW_TEXT = 'new-text'
const SET_STATUS = 'set-status'

let inithialState = {
    Data: {
        aboutMe : '' ,
        fullName: '',
        photos: {
            small:'https://avatars.mds.yandex.net/i?id=21dcf193f9d4ea021c56529f8af1956a46b3ed81-10844270-images-thumbs&n=13',
            large: 'https://avatars.mds.yandex.net/i?id=21dcf193f9d4ea021c56529f8af1956a46b3ed81-10844270-images-thumbs&n=13'
        }
    },
    id: 0, 
    postData : [
        {id:1 , messege:',           ', like: '100', disslike: '50'},
        {id:2 , messege:'z,         ', like: '150', disslike: '500'},
        {id:3 , messege:',         ', like: '160', disslike: '10'},
        {id:4 , messege:', asdv        ', like: '90', disslike: '67'}
    ]
}

export const profileReducer = (state = inithialState , action) => {
    let stateCopy
    
    switch(action.type) {
        case ADD_POST :
            let newPost = { id: state.postData[state.postData.length - 1].id + 1, messege: action.text, like: '90', disslike: '67' };
            stateCopy = {...state};
            stateCopy.postData = [...state.postData];
            stateCopy.postData.push(newPost);
            console.log(stateCopy);
            return stateCopy;
        case PROFILE_UPDATE :
            stateCopy = {...state}
            stateCopy.id = action.id
            return stateCopy
        case SET_DATA :
            stateCopy = {...state}
            stateCopy.Data = {...action.data}
            return stateCopy
        case SET_ID :
            stateCopy = {...state}
            stateCopy.id = action.id['*']
            return stateCopy
        case NEW_TEXT :
            stateCopy = {...state}
            stateCopy.Data.aboutMe = action.text
            return stateCopy
        case SET_STATUS : 
            stateCopy = {...state}
            stateCopy.Data.aboutMe = action.status
            return stateCopy
        default : return state;
    }
}

export const addPostActionCreator = (text) => ({type : ADD_POST,text:text})

export const setData = (data) => ({type:SET_DATA , data:data})

export const setId = (id) => ({type:SET_ID, id: id})

export const setStatus = (status) => ({type: SET_STATUS, status:status})


export const getDataThunk = (id) => {
    return async (dispatch) => {
        let data = await ProfileApi.getUserData(id)

        dispatch(setData(data))
        dispatch(stopLoader())
        
    }
}

export const putStatusThunk = (status) => {
    return async (dispatch) => {
       let data = await ProfileApi.updateStatus(status)
           
       dispatch(stopLoader())  
       
    }
}

export const getStatusThunk = (id) => {
    return async (dispatch) => {
        let data = await ProfileApi.getStatus(id)

        dispatch(setStatus(data))
        dispatch(stopLoader())
       
    }
}