import React from "react";
import { useEffect } from "react"
import s from './Window.module.css'
import { NavLinkCreator } from './../../comon/NavLink/NavLink';

let Window = React.memo( (props) => {

    useEffect(() => {
        if(props.name !== ''){
            props.stopLoader()
        }
      },[props.name,props.stopLoader])
    
    let Click = () => {
        props.authCompleteAC(false);
        props.startLoader();
    }

    return (
        <div className={s.window }style={props.authComplete ? { display: 'block' } : { display: 'none' }}>
            <div className={s.window__inner}>
                <p className={s['window__inner-text']}>{props.name},   !</p>
                {NavLinkCreator(Click,s['window__inner-btn'], '', `/profile/${props.id}`)}
            </div>
        </div>)
})

export default Window