import React from "react";
import Sitebar from './sitebar';
import { connect } from "react-redux";
import { startLoader } from './../../redux/finding-reducer';
import { getNameData } from "../../redux/redux-select/sitebat-reselector";
import { getId } from "../../redux/redux-select/auth-reselect";

let f1 = (state) => {
    
    return {
        state: getNameData(state),
        id : getId(state)
    }
    
}

const sitebar = connect(f1, {
    startLoader
})(Sitebar)

export default sitebar;

