import React from "react";
import s from './LoginForm.module.css'
import { maxLength, required } from './../../comon/validator/validator';
import { LoginInput, PasswordInput } from './../../comon/input/Input';
import { FieldCreation } from './../../comon/Field/Field';
import { Field } from "redux-form";

const LoginForm = React.memo( ({handleSubmit,status}) => {

    return (
        <form onSubmit={handleSubmit} className={s.login}>
            <p className={s.login__text}> Email</p>
            {/* {FieldCreation(`${s.login__input}`,"login","login",[required , maxLength(50)],LoginInput) }
            <p className={s.login__text}> </p>
            {FieldCreation(`${s.login__input}`,"password","password",[required , maxLength(20)],PasswordInput) } */}
            <Field  className={s.login__input} placeholder="login" name="login" validate={[required , maxLength(50)]} component={LoginInput} />
            <p className={s.login__text}> </p>
            <Field className={s.login__input} placeholder="password" name="password" validate={[required , maxLength(20)]} component={PasswordInput} />
            <button className={s.login__btn}>
                
            </button>
        </form>
    )
} )

export default LoginForm