import React from "react";
import App from "./App";
import { connect } from 'react-redux';
import { authCompleteAC } from './redux/auth-reducer';
import { setFinging } from "./redux/redux-select/finding-reselect";
import { stopLoader,startLoader } from './redux/finding-reducer';

let f1 = (state) => {
    return {
        toFinding: setFinging(state),       
    }
}

const AppContainer = connect(f1, {
    authCompleteAC,
    stopLoader,
    startLoader,
})(App)

export default AppContainer